<template>
<div class="fx-index">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-tips">请选择疾病：</div>
    <div :style="{height:clientHeight+'px',overflow:'hidden'}">
        <div class="point-list" :style="{height:clientHeight+'px'}">
            <div v-for="(value,i) in pointList" :key="value.id" @click.stop.prevent="showChild(value.children,i)">
                <div :class="activeIndex == i?'activeItem':''">{{value.content}}</div>
            </div>
        </div>
        <div class="child-list" :style="{height:clientHeight+'px'}" v-if="pointList!==null && pointList.length>0 && activeIndex!==null">
            <div class="disease" v-for="(value,index) in pointList[activeIndex].children" @click="choseDisease(activeIndex,index,value.choosed)" :key="value.id">
                <div :class="value.choosed?'activeItem':''">{{value.content}}</div>
                <div v-if="value.choosed == 1" class="choseIcon"></div>
            </div>
        </div>
    </div>
    <div class="fx-btns">
        <div class="preBtn" v-if="!showMore" @click="showMoreIll">查看已选疾病</div>
	    <div class="preBtn" v-if="showMore" @click="showMoreIll">收起</div>
        <div class="nextBtn" @click="submitDiease()">选好了</div>
    </div>
    <div class="fix-results" v-if="showMore">
        <div class='mask' @click="showMoreIll"></div>
        <div class='fix-main'>
            <div class="titile">
                <span>被保险人所患疾病：</span>
            </div>
            <div class="list">
                <div v-if="value.choosed==1" v-for="(value, index) in pointList" :key="index">
                    <div class="list-item" v-if="p.choosed==1" v-for="(p,n) in value.children" @click="choseDisease(index,n,1)" :key="n">
                        <div class="list-content">{{p.content}}</div>
                        <img class="close" src="../../../static/images/delete1.png"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import Vue from "vue";
import headerText from "@/components/header";
import { mapMutations } from "vuex";
import { mapGetters } from "vuex";
import { Indicator } from 'mint-ui';
export default {
    name:'index',
    data(){
        return{
            proCode:null,
            orderId:null,
            showMore:false,
            hasQuestion:false,
            pointList:null,
            diseaseList:[],
            headerTitle:'智能核保',
            clientHeight:10,
            activeIndex:0,
        }
    },
    created() {

    },
    methods: {
        //展示更多
        showMoreIll() {
            if (!this.showMore) {
                var result = this.getAllChoose();
                if (!result || result.length == 0) {
                    this.$popup.msg("您尚未选择任何疾病！")
                    return;
                }
            }
            this.showMore = !this.showMore;
        },
        //找到 type = 4
        findType4(arr){
            let type4arr = [];
            let findTypeList = function(data){
                data.map(item=>{
                    if(item.children.length>0 && item.children[0].type === 4){
                        type4arr.push(item);
                    }
                    else{
                        findTypeList(item.children);
                    }
                })
            }
            findTypeList(arr);
            return type4arr;
        },
        //提交疾病
        submitDiease(){
            //flag 这里要判断 ????
            var result = this.getAllChoose();
            if (!result || result.length == 0) {
                this.$popup.msg("请您先选择疾病！")
                return
            }
            //判断是否有 type = 4
            let type4arr = this.findType4(result);
            if(type4arr.length>0){//有二级疾病
                sessionStorage.setItem("QUES_ILLS",JSON.stringify(result));
                this.$router.push({path:'/fuxing/index1'});
            }
            else{//无二级疾病
                this.hasQuestion=false;
                this.findQuestions(result);
                console.log("this.hasQuestion",this.hasQuestion);
                console.log("result",result);
                if(!this.hasQuestion){
                    //没有问题的情况下，直接跳过问答页和问答确认页 到结果页面
                    sessionStorage.setItem("QUES_ILLS",JSON.stringify(result));
                    sessionStorage.setItem("TMP_QUES_ILLS",JSON.stringify(result));
                    console.log("result",result);
                    //获取选中疾病的答案id
                    var tmpIdList = [];
                    let findTmpIdList = function(data){
                        data.map(item=>{
                            if(item.choosed === 1 && item.type === 3 && item.children.length === 0){
                                console.log("item",item);
                                tmpIdList.push(item.id);
                                // if(item.children.length>0){
                                //     findTmpIdList(item.children);
                                // }
                            }
                            else if(item.choosed === 1 && item.children.length>0){
                                findTmpIdList(item.children);
                            }
                            else if(item.choosed === 1 && item.children.length === 0){
                                console.log("item",item);
                                tmpIdList.push(item.id);
                            }
                        })
                    }
                    let QUES_ILLS = result;
                    let tmp = findTmpIdList(QUES_ILLS);
                    console.log("tmpIdList",tmpIdList);
                    this.chosedIllIds = tmpIdList;
                    //获取核保结果
                    let HEALTHY_TYPE = sessionStorage.getItem("HEALTHY_TYPE") === '1'?2:1;
                    let THAT = this;
                    Indicator.open({
                        text: '加载中...',
                        spinnerType: 'fading-circle'
                    });
                    THAT.$http.getHttp({
                        name:'fxResults',
                        params:{
                            type:HEALTHY_TYPE,
                            ids:tmpIdList
                        },
                        orderId:THAT.orderId,
                        type:'post'
                    },
                    res => {
                        Indicator.close();
                        let status = res.data.status;
                        //返回的唯一标识 requestId
                        //let requestId = res.data.requestId;
                        let errorContent = res.data.errorList;
                        if(errorContent){
                            sessionStorage.setItem("errorContent",JSON.stringify(errorContent));
                        }
                        //sessionStorage.setItem("REQUESTID",requestId);
                        let resultType = status === 100?'?resultType=1':status === 101?'?resultType=0':status === 102?'?resultType=2':status === 103 ? '?resultType=4':status === 201 ? '?resultType=3':''
                        THAT.$router.push({path:'/fuxing/result'+resultType})
                        console.log(res);//100 通过 101 拒保 102 除责 103人工核保 201数据不正确
                    })
                }
                else{
                    this.initList(result,2,3);
                    console.log("result",result);
                    sessionStorage.setItem("QUES_ILLS",JSON.stringify(result));
                    sessionStorage.setItem("TMP_QUES_ILLS",JSON.stringify(result));
                    //进入回答问题页面
                    console.log("进入回答问题页面");
                    this.$router.push({path:'/fuxing/question'});
                    sessionStorage.setItem("TYPE4_DATA",JSON.stringify([]));
                }
            }
        },
        findQuestions(data){
            data.map(item=>{
                if(item.type==2){
                    this.hasQuestion=true;
                    return false;
                }
                if(item.children && item.children.length>0)this.findQuestions(item.children)
            })
        },
        initList(data,type,level){
            data.map((item,index) => {
                if(item.type==type && item.level==level){
                    item.choosed=2;
                }
                if(item.children && item.children.length>0)this.initList(item.children,type,level);
            });
        },
        // 获取默认选择数组
        getAllChoose() {
            var list = JSON.parse(JSON.stringify(this.pointList));
            if(list && list.length>0){
                var filterData = list.filter((item) => {
                    item.children = item.children.filter(t => t.choosed);
                    return item.choosed == 1;
                });
                return filterData;
            }
            else{
                return [];
            }
        },
        //点击选择疾病
        choseDisease(fatherIndex, index, old) {
            console.time('a')
            this.pointList[fatherIndex].children[index].choosed = old == 1 ? 0 : 1;
            let hasCheckFather = false;
            if (old != 1) {
                hasCheckFather = true;
            } else {
                this.pointList[fatherIndex].children.map((item, n) => {
                    if (item.choosed == 1) {
                        hasCheckFather = true;
                        return false;
                    }
                })
            }
            this.pointList[fatherIndex].choosed = hasCheckFather ? 1 : 0;
            console.timeEnd('a')
        },
        //点击左边获取右边
        showChild(list,index){
            this.activeIndex = index;
            this.$forceUpdate();
        },
        //点击header
        onHeaderBtn(headerText){
            this.$router.go(-1);
        },
        //请求疾病列表
        getPointList(){
            let THAT = this;
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            });
            THAT.$http.getHttp({
                name:'fxIllList',
                params:{},
                orderId:THAT.orderId,
                type:'get'
            },
            res => {
                Indicator.close();
                if(THAT.healthyType == 0 && res.data.holderDiseaseNodes.length>0){
                    let pointList = THAT.setAllChoose(res.data.holderDiseaseNodes);
                    THAT.pointList = pointList;
                    //THAT.diseaseList = THAT.pointList[THAT.activeIndex].children;
                }
                else if(THAT.healthyType == 1 && res.data.insureDiseaseNodes.length>0){//insureDiseaseNodes
                    let pointList = THAT.setAllChoose(res.data.insureDiseaseNodes);
                    THAT.pointList = pointList;
                    //THAT.diseaseList = THAT.pointList[THAT.activeIndex].children;
                }
            })
            console.log("THAT.pointList",THAT.pointList);
        },
        // 设置默认选择项
        setAllChoose(data) {
            data.map((item, index) => {
                item.choosed = 0;
                if (item.children && item.children.length > 0) {
                    this.setAllChoose(item.children);
                }
            });
            return data;
        },
    },
    computed:{

    },
    components:{
        "header-text":headerText
    },
    mounted(){
        let healthyType = this.$route.query.healthyType,
        orderId = this.$route.query.policyId,
        jumpKey = this.$route.query.key,
        proCode = this.$route.query.proCode;
        this.healthyType = healthyType;
        this.orderId = orderId;
        sessionStorage.setItem("HEALTHY_TYPE",healthyType);
        sessionStorage.setItem("ORDER_ID",orderId);
        sessionStorage.setItem("JUMP_KEY",jumpKey);
        sessionStorage.setItem("proCode",proCode);
        this.clientHeight = window.innerHeight;
        this.getPointList();
        console.log("this.pointList",this.pointList);
    }
};
</script>

<style lang="scss">
.fx-index{
    width:r(750);
    height:100%;
    background:#fff;
    position: relative;
    overflow: hidden;
    .fx-tips{
        height:r(88);
        line-height:r(88);
        font-size:r(28);
        border-bottom:1px solid #dddddd;
        color:#4580f6;
        // font-weight: bold;
        padding-left: r(27);
    }
    .fix-results {
        position: fixed;
        bottom: r(92);
        width: 100%;
        height:100%;
        .mask {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, .6);
        }

        .fix-main {
            position: absolute;
            width: 100%;
            height: 50%;
            bottom: 0;
            left: 0;
            background: #fff;
            overflow: auto;
            padding:r(20);
        }

        .titile {
            position: relative;

            &>text {
                color: #000;
                font-size: r(28);
                height: r(30);
                line-height: r(30);
                margin-top: r(30);
                padding-left: r(30);
            }
        }

        .list {
            display: block;
            padding-top: r(20);

            .list-item {
                margin-left: r(22);
                position: relative;
                overflow: hidden;
                margin-bottom: r(10);
                display: inline-block;

                .close {
                    position: absolute;
                    width: r(22);
                    height: r(23);
                    top: r(17);
                    right: r(27);
                }

                .list-content {
                    background: #dae6fd;
                    color: #4580f6;
                    font-size: r(26);
                    height: r(55);
                    line-height: r(54);
                    padding-left: r(20);
                    padding-right: r(55);
                    border-radius: r(30);
                    max-width: r(340);
                    white-space: pre-wrap;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -ms-text-overflow: ellipsis;
                    -o-text-overflow: ellipsis;
                    display: inline-block;
                }
            }
        }
    }
    .fx-btns{
        height:r(92);
        position:fixed;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        div{
            width:50%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .preBtn{
            background:#fff;
            color:#333;
            line-height:r(92);
        }
        .nextBtn{
            background:#4580f6;
            color:#fff;
            line-height:r(92);
        }
    }
    .point-list,.child-list{
        height:100%;
        width:r(338);
        background:#f6f6f6;
        overflow-y: auto;
        float: left;
        padding-bottom:r(330);
    }
    .child-list{
        width:r(412);
        background:#fff;
    }
    .point-list,.child-list{
        &>div{
            // height:r(88);
            // line-height:r(88);
            font-size:r(28);
            color:#333;
            position:relative;
            &>div{
                white-space: pre-wrap;
                padding-left: r(27);
                line-height:r(40);
                padding:r(20);
                text-align:justify;
            }
        }
        .disease{
            height:auto;
            position: relative;
            border-bottom:1px solid #ddd;
            &>div{
                height:auto;
            }
            .activeItem{
                color:#4580f6;
            }
            .choseIcon{
                width: r(30);
                height:r(22);
                background:url("../../../static/images/choseIcon.png") center no-repeat;
                background-size:r(30) r(22);
                position:absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
    .point-list{
        &>div{
            &>div{
                padding-right:r(20);
            }
        }
        .activeItem{
            background: #ffffff;
            color:#4580f6;
        }
    }
    .child-list{
        &>div{
            &>div{
                padding-right:r(60);
            }
        }
    }
}
</style>


